.control-board-wrapper{
    display:flex;

}
.control-board-element-explain-wrapper{
    display:grid;
    grid-template-columns:repeat(3,auto);
    grid-template-rows:repeat(4,1fr);
    width:fit-content;
    height:fit-content;
}
.current-element-wrapper{
    grid-row: 1 / 5;
    display:flex;
    align-items:center;
    justify-content:center;
}
.img-line{
    width:0.6cm;
    height:100%;
    grid-column:2 / 3;
    grid-row:1 / 5;
}
.element-explain{
    grid-column: 3 / 4;
    text-align:center;
    padding:2px 5px;
    border-radius:0.5cm;
    border:0.5px solid #888888;
    font-size:0.3cm;
    margin:2px;
}
select{
    
}
.current-element{
    border:0.5px solid #333333;
}

/**tag*/
.control-board-tag-wrapper{
    margin:5px;
    display:flex;
}
.control-board-tag-group{
    display:block;
}
.control-board-tag{
    text-align:center;
    padding:2px 5px;
    border-radius:0.5cm;
    border:0.5px solid #888888;
    font-size:0.3cm;
    margin:2px;
}
.tag{
    box-shadow:0 0 10px #AC73FF;
    background-color:#AC73FF;
}